/**
 * 基础菜单 视频管理
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>视频管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="formInline.search" placeholder="输入视频名称"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-add" @click="replyFormVisible=true">新增</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" id="ou" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text="拼命加载中" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="视频名称" width="100">
      </el-table-column>
      <el-table-column sortable prop="type" label="视频类型" width="100">
      </el-table-column>
      <el-table-column sortable prop="editUser" label="初始上传人" width="120">
      </el-table-column>
      <el-table-column sortable prop="createdAt" label="上传时间" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.createdAt|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="describe" label="视频描述" width="300">
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">查看视频</el-button>
          <el-button size="mini" type="danger" @click="deleteVideo(scope.$index, scope.row)">删除/停用</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>

    <!-- 查看界面 -->
    <el-dialog title="视频" :visible.sync="editFormVisible" width="30%" @click="closeVideoDialog"
      @open="openVideoDialog" append-to-body>
      <div>
        <video ref="videoPlay" width="100%" height="100%" :src="viewVideoUrl" autoPlay preload="auto" controls>
        </video>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeVideoDialog">取消</el-button>
      </div>
    </el-dialog>
    <!-- 编辑界面 -->
    <el-dialog v-loading="loading" title="新增教学视频" :visible.sync="replyFormVisible" width="30%" @click="closeDialog"
      append-to-body>
      <el-form :model="videoForm" :rules="rules">
        <el-form-item label="视频名称" prop="name" :label-width="formLabelWidth">
          <el-input v-model="videoForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="视频描述" prop="describe" :label-width="formLabelWidth">
          <el-input v-model="videoForm.describe" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="视频文件" :label-width="formLabelWidth">
          <el-upload class="upload-demo" ref="upload" accept=".mp4" :multiple="false" :show-file-list="ture"
            action="void" :http-request="customUpload" auto-upload :limit="1">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">保存
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { GetVideoesList, GetVideoById, AddVideo,DeleteVideo,UploadFile ,GetVideoLinkById} from '../../api/basisMg'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      replyFormVisible:false,
      title: '添加',
      drawer: false,
      marks:['2022-07-01','2022-07-02','2022-07-03'],
      editForm: {
        deptId: '',
        deptName: '',
        deptNo: '',
        token: localStorage.getItem('logintoken')
      },
      videoForm:{},
      uploadFileLink:{},
      viewVideoUrl:'',
      // rules表单验证
      rules: {
        name: [
          { required: true, message: '请输入视频名称', trigger: 'blur' }
        ],
        describe: [{ required: true, message: '请输入视频描述', trigger: 'blur' }]
      },
      formInline: {
        pageIndex: 1,
        pageSize: 10,
        search: ''
      },
      // 删除部门
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      userparm: [], //搜索权限
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取公司列表
    getdata(parameter) {
      this.loading = true
      GetVideoesList(parameter)
        .then(res => {
          this.loading = false
          if (res.data.code <=0) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.listData = res.data.data.list
            // 分页赋值
            this.pageparm.currentPage = this.formInline.pageIndex
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.data.count
          }
        }).catch(err => {
          this.loading = false
          this.$message.error('加载失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.pageIndex = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //显示编辑界面
    handleEdit: function (index, row) {
      this.viewVideoUrl = ''//
      this.loading = true;
      const that = this;
      GetVideoLinkById(row.id).then(res => {
        if (res.data.code > 0) {
          console.log("打开视频" + res.data.data)
          this.viewVideoUrl = res.data.data
          this.editFormVisible = true
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },
    //提醒打卡
    remindClock : function(index, row) {
       this.$message({
            type: 'success',
            message: '提醒患者成功'
          })
    },
    // 编辑、增加页面保存方法
    async submitForm(editData) {
       const parms = this.videoForm;
       const file = this.$refs.upload._data.uploadFiles[0].raw;
       this.loading = true;
       const fileId = await this.uploadFile(this.uploadFileLink, file, file.name);
       if (!fileId) {
         this.$message.error('文件上传失败!---云托管上传失败');
         return;
       }
       parms.fileId = fileId
       parms.type = '教学视频' //01
       AddVideo(parms).then(res => {
         this.loading = false;
         if (res.data.code > 0) {
           this.$message.success('文件上传成功!');
         } else {
           this.$message.error('文件上传失败!');
         }
         this.closeDialog();
         this.videoForm = {};
         this.getdata(this.formInline);
       }).catch(err=>{
        this.$message.error(err);
        this.loading = false;
       })

    },
    customUpload(file) {
      this.loading = true
      UploadFile(file.file.name).then(res => {
        if (res.data.code >0) {
          this.uploadFileLink = res.data.data
        } else {
          this.$message.error('文件上传失败!');
        }
        this.loading = false
      })
    },
    // 删除视频
    deleteVideo(index, row) {
      this.$confirm('确定此视频吗?', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          DeleteVideo(row.id)
            .then(res => {
              if (res.data.code > 0) {
                this.$message({
                  type: 'success',
                  message: '已删除!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('删除失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '选择取消'
          })
        })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.loading = false
      this.editFormVisible = false
      this.replyFormVisible = false
    },
    // 关闭视频播放弹出框
    closeVideoDialog(){
      this.$refs.videoPlay.pause();
      this.closeDialog();
    },
    openVideoDialog(){

    },
    async uploadFile(result, file, filePath) {
      const data = new FormData();
      data.append("key", result.key);
      data.append("Signature", result.authorization);
      data.append("x-cos-security-token", result.token);
      data.append("x-cos-meta-fileid", result.cos_file_id);
      data.append("file", file, filePath);
      const fileraw = await this.request(result.url, 'POST', data)
      if (fileraw != '') {
        return '';
      }
      return result.fileid
    },
    request(url, method = 'GET', data = null) {
      return new Promise(function (resolve, reject) {
        const xhr = new XMLHttpRequest();
        xhr.withCredentials = true;
        xhr.addEventListener("readystatechange", function () {
          if (this.readyState === 4) {
            resolve(this.responseText)
          }
        });
        xhr.open(method, url);
        xhr.send(data);
      })
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
</style>





