//response 拦截器
service.interceptors.response.use(
  response => {
    /**
     * code为非20000是抛错 可结合自己业务进行修改
     */
    // 302	登录已失效
    var data = response.data
    if (data.code == 401 || data.code == 302) {
      //this.$message.error('登录失效，重新登录')
      //clearCacheEnterLogin()
      localStorage.setItem("userInfo", "")
      router.push('/login')
    } else {
      return response
    }
  },
  error => {
    if (error.response) {
      const response = error.response
      if (response.status == 500) {
        this.$message.error('网络错误，请检查您的网络')
      } else if (response.data && response.data.msg) {
        this.$message.error(response.data.msg)
      }
    }
    return Promise.reject(error)
  }
)
// 登录请求方法
const loginreq = (method, url, params) => {
    return axios({
        method: method,
        url: url,
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
        },
        data: params,
        traditional: true,
        transformRequest: [
            function(data) {
                let ret = ''
                for (let it in data) {
                    ret +=
                        encodeURIComponent(it) +
                        '=' +
                        encodeURIComponent(data[it]) +
                        '&'
                }
                return ret
            }
        ]
    }).then(res => res.data);
};
// 通用公用方法
const req = async (method, url, params) => {
    const res = await service({
    method: method,
    url: url,
    headers: {
      'Content-Type': 'application/json',
      token: localStorage.getItem('logintoken')
    },
    data: params
  });
  return res;
};
// 通用公用方法
const reqFile = async (method, url, params) => {
  const res = await service({
  method: method,
  url: url,
  headers: {
    'Content-Type': 'application/json',
    token: localStorage.getItem('logintoken')
  },
  data: params,
  responseType:"blob"
});
return res;
};


export {
    loginreq,
    req,
    reqFile
}



