/**
 * 基础菜单 患者管理
 */
<template>
  <div>
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>患者管理</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 搜索筛选 -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="搜索：">
        <el-input size="small" v-model="formInline.search" placeholder="输入患者名称"></el-input>
      </el-form-item>
      <el-form-item>
      <el-button size="small" type="primary" icon="el-icon-search" @click="search">搜索</el-button>
      <el-date-picker v-model="daterange" type="daterange" align="right" start-placeholder="开始日期" end-placeholder="结束日期"
        :default-time="['12:00:00', '23:59:59']">
      </el-date-picker>
        <el-button size="small" type="primary" icon="el-icon-document" @click="exportExcel">导出Excel</el-button>
      </el-form-item>
      <el-form-item >
        <el-button size="small" type="danger" icon="el-icon-bell" @click="oneTouchRemindClock">一键提醒</el-button>
      </el-form-item>
      <el-form-item >
        <el-button size="small" type="primary" icon="el-icon-bell" @click="oneTouchCustomRemindClock">一键提醒自定义</el-button>
      </el-form-item>
    </el-form>
    <!--列表-->
    <el-table size="small" id="ou" :data="listData" highlight-current-row v-loading="loading" border
      element-loading-text="拼命加载中"
      :row-class-name="tableRowClassName"
      @selection-change="handleSelectionChange" style="width: 100%;">
      <el-table-column align="center" type="selection" width="60">
      </el-table-column>
      <el-table-column sortable prop="name" label="患者姓名" width="100">
      </el-table-column>
      <el-table-column sortable prop="sex" label="性别" width="70">
      </el-table-column>
      <el-table-column sortable prop="createdAt" label="初始登记时间" width="150">
        <template slot-scope="scope">
          <div>{{scope.row.createdAt|timestampToTime}}</div>
        </template>
      </el-table-column>
      <el-table-column sortable prop="sameDayIsCheck" align="center" label="当天是否签到" width="120">
      </el-table-column>
      <el-table-column  label="当月详情" width="120">
        <template slot-scope="scope">
          <el-button @click="openCalendar(scope.row)" size="mini" type="primary" style="margin-left: 16px;">打开日历
          </el-button>
        </template>
      </el-table-column>
      <el-table-column  prop="teachName" label="当前治疗视频" width="120">
      </el-table-column>
      <el-table-column  label="备注" width="80">
        <template slot-scope="scope">
          <el-button type="text"  @click="editRemark(scope.row)" size="mini" >查看编辑</el-button>
        </template>
      </el-table-column>
      <el-table-column align="center" label="操作" min-width="350">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="remindClock(scope.$index, scope.row)">提醒</el-button>
          <el-button type="text" size="mini" @click="handleEdit(scope.$index, scope.row)">查看当天</el-button>
          <el-button size="mini" type="success" @click="openSetTeachingVideo(scope.$index, scope.row)">设置治疗视频</el-button>
          <el-button size="mini" type="danger" @click="deletePatient(scope.$index, scope.row)">删除</el-button>
          <el-button size="mini" type="danger" @click="dataArchive(scope.row)">归档</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页组件 -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>

    <el-drawer ref="refdrawer" :visible.sync="drawer" size="35%" :with-header="false">
      <span style="color:red;margin:15px 15px">备注：点击已签到可以播放当天视频</span>
      <el-calendar>
        <!-- 这里使用的是 2.5 slot 语法，对于新项目请使用 2.6 slot 语法 data.day yyyy-MM-dd-->
        <template slot="dateCell" slot-scope="{date, data}">
          <p :class="data.isSelected ? 'is-selected' : ''">
            {{ data.day.split('-').slice(1).join('-') }}
          </p>
          <div v-for="(item,index) in marks" :key="index" style="width:0px;height:0px">
            <el-tag v-if="data.day == item.date" size="mini" :disable-transitions="false"
              @click="handleEdit('',{todayClockInId:item.id,lastCheckInVideos:item.videos})">已签到
            </el-tag>
          </div>
        </template>
      </el-calendar>
    </el-drawer>
    <!-- 查看界面 -->
    <el-dialog :title="videoTitle" :visible.sync="editFormVisible" width="30%" @click="closeVideoDialog"
      @open="openVideoDialog" append-to-body>
      <div>
        <video ref="videoPlay" width="100%" height="100%" :src="viewVideoUrl" autoPlay preload="auto" controls>
        </video>
      </div>
      <div>
        <el-button-group>
          <el-button v-for="btn in currentDayVideos" :key="btn.id" type="danger" @click="changeVideo(btn.id)" >
           {{btn.name}}
          </el-button>
      </el-button-group>
      </div>
      <div>
        <el-form :model="editForm">
          <el-form-item label="评价" label-width="80">
            <el-input v-model="editForm.replyContent" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" :loading="loading" class="title" @click="submitForm('editForm')">回复患者
        </el-button>
        <el-button size="small" @click="closeVideoDialog">取消</el-button>
      </div>
    </el-dialog>
    <!-- 编辑设置界面 -->
    <el-dialog title="设置视频" :visible.sync="settingFormVisible" width="30%" @click="closeDialog" append-to-body>
      <el-form>
        <el-form-item label="教学视频" prop="roles">
          <el-select size="small" v-model="settingVideo.tid" multiple placeholder="请选择">
            <el-option v-for="item in settingVideoOptions" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-timeline reverse="true">
        <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
          {{activity.content}}
        </el-timeline-item>
      </el-timeline>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog">取消</el-button>
        <el-button size="small" type="primary" :loading="loading" class="title" @click="settingTeachingVideo()">保存
        </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import { PatientsList, GetVideoLinkById, GetVideoesList,PatientsDelete,
  GetClockInByOpenId, GetPatientSetting, SetPatientVideo,
  Patientfeedback,RemindClockIn,exportExcel,EditRemark,DataArchive,RemindCustomClockIn } from '../../api/basisMg'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch开启
      fshow: false, //switch关闭
      loading: false, //是显示加载
      editFormVisible: false, //控制编辑页面显示与隐藏
      settingFormVisible:false,//控制编辑页面显示与隐藏
      settingVideo:{pid:'',tid:''},
      settingVideoOptions:[],
      viewVideoUrl:'',
      replyFormVisible:false,
      videoTitle:'视频',
      title: '添加',
      drawer: false,
      todayClockInId:'',//当天签到id
      marks:[],
      editForm: {
        replyContent: ''
      },
      currentDayVideos:[],
      activities: [],
      form:{},
      daterange:'',
      // rules表单验证
      rules: {
        deptName: [
          { required: true, message: '请输入部门名称', trigger: 'blur' }
        ],
        deptNo: [{ required: true, message: '请输入部门代码', trigger: 'blur' }]
      },
      formInline: {
        pageIndex: 1,
        pageSize: 10,
        search: '',
        token: localStorage.getItem('logintoken')
      },
      multipleSelection:[],
      userparm: [], //搜索权限
      listData: [], //用户数据
      // 分页参数
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 注册组件
  components: {
    Pagination
  },
  /**
   * 数据发生改变
   */

  /**
   * 创建完毕
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 里面的方法只有被调用才会执行
   */
  methods: {
    // 获取公司列表
    getdata(parameter) {
      this.loading = true
      /***
       * 调用接口，注释上面模拟数据 取消下面注释
       */
      PatientsList(parameter)
        .then(res => {
          this.loading = false
          if (res.data.code <0) {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          } else {
            this.listData = res.data.data.list
            // 分页赋值
            this.pageparm.currentPage = this.formInline.pageIndex
            this.pageparm.pageSize = this.formInline.pageSize
            this.pageparm.total = res.data.data.count
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('失败，请稍后再试！')
        })
    },
    // 分页插件事件
    callFather(parm) {
      this.formInline.pageIndex = parm.currentPage
      this.formInline.pageSize = parm.pageSize
      this.getdata(this.formInline)
    },
    // 搜索事件
    search() {
      this.getdata(this.formInline)
    },
    //编辑备注
    editRemark(row){
      var that = this;

      this.$prompt('备注:'+row.remark, '请编辑输入备注', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputType:'textarea',
          inputValue:row.remark
        }).then(({ value }) => {
          EditRemark({id:row.id,remark:value}).then(res => {
            if (res.data.code > 0) {
              this.$message({
                type: 'success',
                message: '成功'
              })
            } else {
              this.$message({
                type: 'success',
                message: + res.data.message
              })
            }
          })
          that.getdata(that.formInline);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
    },
     //编辑备注
     dataArchive(row){
      var that = this;
      this.$confirm('此操作将归档该患者, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          DataArchive({id:row.id}).then(res => {
            if (res.data.code > 0) {
              this.$message({
                type: 'success',
                message: '成功'
              })
              that.getdata(that.formInline);
            } else {
              this.$message({
                type: 'success',
                message: + res.data.message
              })
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消输入'
          });
        });
    },
    //显示编辑界面
    handleEdit: function (index, row) {
      if(row.lastCheckInVideos==null || row.lastCheckInVideos.length == 0){
        this.$message({
            type: 'info',
            message: '没有视频'
          })
          return
      }
      this.viewVideoUrl = ''//
      this.loading = true;
      const that = this;
      GetVideoLinkById(row.lastCheckInVideos[0].id).then(res => {
        if (res.data.code > 0) {
          console.log("打开视频" + res.data.data)
          this.viewVideoUrl = res.data.data
          this.todayClockInId = row.todayClockInId
          this.currentDayVideos = row.lastCheckInVideos
          this.editFormVisible = true
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
        this.loading = false;
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },
    changeVideo(id){
      GetVideoLinkById(id).then(res => {
        if (res.data.code > 0) {
          console.log("打开视频" + res.data.data)
          this.viewVideoUrl = res.data.data
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
        this.loading = false;
      }).catch(err => {
        this.$message.error(err);
        this.loading = false;
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    oneTouchRemindClock(){
      if(this.multipleSelection.length == 0){
        this.$message({
            type: 'success',
            message: '请先选择需要提醒的患者！'
          })
          return;
      }
      this.multipleSelection.forEach(row=>{
        this.remindClock(null,row);
      })
    },
    oneTouchCustomRemindClock(){
      if(this.multipleSelection.length == 0){
        this.$message({
            type: 'success',
            message: '请先选择需要提醒的患者！'
          })
        return;
      }
      var that = this;
      this.$prompt('请输入提醒打卡文字', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputType: 'text',
      }).then(({ value }) => {
        that.multipleSelection.forEach(row => {
          that.customClockIn(row, value);
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消，异常'
        });
      });
    },
   customClockIn(row,val){
    RemindCustomClockIn({openId:row.openId,remark:val})
      .then(res => {
        if (res.data.code > 0) {
          this.$message({
            type: 'success',
            message: '提醒患者：【'+ row.name + '成功'
          })
        } else {
          this.$message({
            type: 'success',
            message: '提醒患者：【'+ row.name + '】'+ res.data.message
          })
        }
      })
        .catch(err => {
          this.loading = false
          this.$message.error( '提醒患者：【'+ row.name + '】失败，请稍后再试！')
        })
   },

    //提醒打卡
    remindClock : function(index, row) {
      RemindClockIn(row.openId)
      .then(res => {
        if (res.data.code > 0) {
          this.$message({
            type: 'success',
            message: '提醒患者：【'+ row.name + '成功'
          })
        } else {
          this.$message({
            type: 'success',
            message: '提醒患者：【'+ row.name + '】'+ res.data.message
          })
        }
      })
        .catch(err => {
          this.loading = false
          this.$message.error( '提醒患者：【'+ row.name + '】失败，请稍后再试！')
        })
    },
  //打开设置教学视频
    openSetTeachingVideo: function (index, row) {
      this.settingVideo.pid = row.id
      this.settingFormVisible = true
      GetPatientSetting(this.settingVideo.pid)
      .then(res => {
          if (res.data.code > 0) {
            this.activities = res.data.data.activities
            this.settingVideo.tid = res.data.data.setTeachVideos.map(o=>o.id)
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
      .catch(err => {
          this.loading = false
          this.$message.error('保存失败，请稍后再试！')
        })

      GetVideoesList({
        pageIndex: 1,
        pageSize: 50,
        search: ''
      })
      .then(res => {
          if (res.data.code > 0) {
            this.settingVideoOptions = res.data.data.list
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
        .catch(err => {
          this.loading = false
          this.$message.error('失败，请稍后再试！')
        })
    },
    // 编辑、增加页面保存方法
    settingTeachingVideo() {
      SetPatientVideo({ pid: this.settingVideo.pid, tid: this.settingVideo.tid })
        .then(res => {
          if (res.data.code > 0) {
            this.settingVideo.pid = ''
            this.settingFormVisible = false
          } else {
            this.$message({
              type: 'info',
              message: res.data.message
            })
          }
        })
        .catch(err => {

          this.loading = false
          this.$message.error('保存失败，请稍后再试！')
        })
    },
    // 编辑、增加页面保存方法
    submitForm() {
      Patientfeedback({
        id: this.todayClockInId,
        content: this.editForm.replyContent
      })
        .then(res => {
          this.loading = false
          this.$message({
              type: 'success',
              message: res.data.message
          })
          this.closeVideoDialog()
        })
        .catch(err => {
          this.loading = false
          this.$message.error('失败，请稍后再试！')
        })
    },
    // 删除公司
    deletePatient(index, row) {
      this.$confirm('确定删除此患者吗?,操作讲无法恢复', '信息', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          PatientsDelete(row.id)
            .then(res => {
              if (res.data.code > 0) {
                this.$message({
                  type: 'success',
                  message: '已删除!'
                })
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.data.message
                })
              }
            })
            .catch(err => {
              this.loading = false
              this.$message.error('删除失败，请稍后再试！')
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '选择取消'
          })
        })
    },
    // 关闭编辑、增加弹出框
    closeDialog() {
      this.loading = false
      this.editFormVisible = false
      this.replyFormVisible = false
      this.settingFormVisible =false
    },
    // 关闭视频播放弹出框
    closeVideoDialog(){
      this.$refs.videoPlay.pause();
      this.closeDialog();
    },
    openVideoDialog(){

    },
    openCalendar(row){
      this.drawer = true
      //获取详情
      GetClockInByOpenId(row.openId).then(res => {
        if (res.data.code > 0) {
         this.marks = res.data.data
        } else {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
      });


    },
    exportExcel() {
      const pamams = {
        date1:this.fermitTime(this.daterange[0]),
        date2:this.fermitTime(this.daterange[1])
      }
      exportExcel(pamams)
      .then(res => {
        try {
          this.$FileSaver.saveAs(res.data,
            "sheetjs.xls"
          );
        } catch (e) {
          if (typeof console !== "undefined") console.log(e, table_write);
        }
        if (res.data.code < 0) {
          this.$message({
            type: 'info',
            message: res.data.message
          })
        }
      }).catch(err => {
        this.$message.error(err);
      });
    },
    fermitTime(time) {
      var now = new Date(time);
      var year = now.getFullYear();
      var mon = now.getMonth() + 1;
      var date = now.getDate();
      if (mon < 10) {
        mon = '0'+mon;
      };
      if (date < 10) {
        date = '0'+date;
      }
      var postDate = year +'-'+mon+'-'+date;
      return postDate;
    },
    tableRowClassName({ row, rowIndex }) {

      if (row.sameDayIsCheck == '是') {
        return 'success-row';
      }
      return '';
    }
  }
}
</script>

<style>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
.el-table .warning-row {
    background: oldlace;
}
.el-table .success-row {
    background: #cff3bb;
}
</style>





