/**
* 左边菜单
*/
<template>
  <el-menu default-active="2" :collapse="collapsed" collapse-transition router :default-active="$route.path" unique-opened class="el-menu-vertical-demo" background-color="#334157" text-color="#fff" active-text-color="#ffd04b">
    <div class="logobox">
      <img class="logoimg" src="../assets/img/logo.png" alt="">
    </div>
    <el-submenu v-for="menu in allmenu" :key="menu.menuid" :index="menu.menuname">
      <template slot="title">
        <i class="iconfont" :class="menu.icon"></i>
        <span>{{menu.menuname}}</span>
      </template>
      <el-menu-item-group>
        <el-menu-item v-for="chmenu in menu.menus" :index="'/'+chmenu.url" :key="chmenu.menuid">
          <i class="iconfont" :class="chmenu.icon"></i>
          <span>{{chmenu.menuname}}</span>
        </el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>
<script>
import { GetCurrentPermission } from '../api/userMG'
export default {
  name: 'leftnav',
  data() {
    return {
      collapsed: false,
      allmenu: [],
      manageMenu: [{
        menuid: 72,
        icon: 'icon-cus-manage',
        menuname: '用户管理',
        menucode:'userManger',
        hasThird: 'N',
        url: 'system/user',
        menus: null
      },
      {
        menuid: 174,
        icon: 'icon-cms-manage',
        menuname: '菜单管理',
        menucode:'moduleManger',
        hasThird: 'N',
        url: 'system/Module',
        menus: null
      },
      {
        menuid: 73,
        icon: 'icon-news-manage',
        menuname: '角色管理',
        menucode:'roleManger',
        hasThird: 'N',
        url: 'system/Role',
        menus: null
      }],
      baseMenun:[{
              menuid: 2,
              icon: 'icon-cat-skuQuery',
              menuname: '患者管理',
              menucode:'patientManger',
              hasThird: 'N',
              url: 'baseManage/Patient',
              menus: null
             },{
              menuid: 21,
              icon: 'icon-order-manage',
              menuname: '视频管理',
              menucode:'videoManger',
              hasThird: 'N',
              url: 'baseManage/VideoManage',
              menus: null
             }]
    }
  },
  // 创建完毕状态(里面是操作)
  created() {
    // 获取图形验证码
    let res = {
      success: true,
      data: [
        {
          menuid: 1,
          icon: 'li-icon-xiangmuguanli',
          menuname: '基础管理',
          hasThird: null,
          url: null,
          menus: []
        },
        {
          menuid: 71,
          icon: 'li-icon-xitongguanli',
          menuname: '系统管理',
          hasThird: null,
          url: null,
          menus: [
            {
              menuid: 77,
              icon: 'icon-promotion-manage',
              menuname: '模板消息管理',
              hasThird: 'N',
              url: 'system/Template',
              menus: null
            }
          ]
        }
      ],
      msg: 'success'
    }
    this.allmenu = res.data
    // 监听
    this.$root.Bus.$on('toggle', value => {
      this.collapsed = !value
    })
  },
  mounted(){
    //获取结果
    GetCurrentPermission()
      .then(res => {
        if (res.data.code == 1) {
          var keys = Object.keys(res.data.data)
           this.manageMenu.forEach(element => {
            if(keys.find(o=>o == element.menucode)){
              this.allmenu[1].menus.unshift(element)
            }
          });
          this.baseMenun.forEach(element => {
            if(keys.find(o=>o == element.menucode)){
              this.allmenu[0].menus.unshift(element)
            }
          });
        }
      }).catch(err => {

      })
  }
}
</script>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 240px;
  min-height: 400px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  border: none;
  text-align: left;
}
.el-menu-item-group__title {
  padding: 0px;
}
.el-menu-bg {
  background-color: #1f2d3d !important;
}
.el-menu {
  border: none;
}
.logobox {
  height: 40px;
  line-height: 40px;
  color: #9d9d9d;
  font-size: 20px;
  text-align: center;
  padding: 20px 0px;
}
.logoimg {
  height: 60px;
}
</style>



